@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: "Roboto", sans-serif, monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Footer {
  background-color: #2c3e50;
  color: #ffffff;
  display: block;
  position: relative;
  padding: 10px;
}

.Footer a {
  color: #ffffff;
}
.Footer .copyRight {
  font-size: 10px;
  display: block;
  position: relative;
  width: 100%;
  text-align: center;
  margin: 15px 10px;
}

.Footer .socialMedia {
  font-size: 15px;
  display: block;
  position: relative;
  text-align: center;
  margin: 0 auto;
  margin: 15px 10px;
}

.Footer .socialMedia .fab,
.Footer .socialMedia .fas {
  display: inline-block;
  position: relative;
  width: 15%;
  font-size: 20px;
  vertical-align: bottom;
}
@media only screen and (min-width: 768px) {
  .Footer .socialMedia .fab,
  .Footer .socialMedia .fas {
    width: 5%;
  }
}

header nav ul li {
  display: inline-block;
  margin: 0px;
  margin: 10px;
  text-align: center;
}
header nav ul li a {
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  font-weight: 500;
}

header {
  background-color: #ff7043;
  padding: 20px 0px;
  width: 100%;
}

header .displayLeft {
  font-size: 30px;
  color: #ffffff;
  font-weight: 800;
  margin-left: 50px;
}

header nav ul {
  text-align: center;
  margin: 0;
}

header .active {
  border-bottom: 2px solid #ffffff;
  border-radius: 2px;
}

header .displayRight {
  color: #ffffff;
}
@media screen and (min-width: 1200px) {
}

.card {
  width: 250px;
  height: 220px;
  display: inline-block;
  position: relative;
  vertical-align: bottom;
  text-align: center;
  margin: 20px 15px;
  background-color: #2c3e50;
  padding: 20px 20px;
  border-radius: 5px;
}

.card .descriptiveText {
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
}

.card h2 {
  padding: 0px;
  margin: 0px;
  color: #ffffff;
  display: inline-block;
  position: relative;
  font-size: 22px;
}

.card .fas {
  margin-right: 10px;
  color: #ffffff;
  font-size: 20px;
}

.card .infoBar {
  width: 250px;
  height: 5px;
  background-color: #ffffff;
  display: block;
  position: relative;
  border-radius: 2px;
}

.card .firstDot {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  position: absolute;
  display: block;
  top: -8px;
  left: 10px;
}

.card .secondDot {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  position: absolute;
  display: block;
  top: -8px;
  right: 0px;
}

.card .visualInfo .day,
.card .visualInfo .avg {
  display: inline-block;
  position: relative;
  color: #ffffff;
}

.card .visualInfo .day {
  margin-right: 20px;
}

.card .deleteIcon {
  position: relative;
  display: block;
  text-align: center;
  margin-top: 30px;
}
.card .cardDataQuantity {
  margin-top: 0px;
}

.card .currentyQuant {
  color: #ffffff;
  font-size: 17px;
  margin-bottom: 40px;
}

.card .fa-trash {
  display: inline-block;
  position: relative;

  font-size: 35px;
  color: #2c3e50;
}

.card input[type="number"] {
  width: 45%;
  margin-right: 15px;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 5px;
  vertical-align: bottom;
  box-sizing: border-box;
  text-align: center;
  font-size: 21px;
  font-weight: 900;
  color: #2c3e50;
  -webkit-appearance: none;
  border: 0px;
}

.card .addStockBtn {
  background-color: #2c3e50;
  padding: 15px;
  border: 0px;
  border-radius: 5px;
  width: 45%;
  box-sizing: border-box;
  color: #ffffff;
  font-size: 13px;
  font-weight: bold;
  -webkit-appearance: none;
  vertical-align: bottom;
}

.card .consumeBtn {
  background-color: #2c3e50;
  position: absolute;
  left: 15px;
  bottom: 15px;
  border: 0px;
  border-radius: 5px;
  width: 90%;
  margin-top: 20px;
  box-sizing: border-box;
  padding: 15px;
  color: #ffffff;
  font-size: 13px;
  font-weight: bold;
  -webkit-appearance: none;
  vertical-align: bottom;
}

.card .roundCorner {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-width: 0 35px 35px 0;
  border-style: solid;
  border-color: #ffffff #ffffff rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 0px rgba(0, 0, 0, 0.2), -1px 1px 1px rgba(0, 0, 0, 0.2);
  display: block;
  width: 0;
  border-radius: 0 0 0 5px;
  width: 0px;
  height: 0px;
}

.card .frontRoundCorner {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-width: 0 35px 35px 0;
  border-style: solid;
  border-color: #ffffff #ffffff rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 0px rgba(0, 0, 0, 0.2), -1px 1px 1px rgba(0, 0, 0, 0.2);
  display: block;
  width: 0;
  border-radius: 0 0 0 5px;
  width: 0px;
  height: 0px;
}

.contactForm input,
.contactForm textarea {
  display: block;
  position: relative;
  width: 80%;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #d0d0d0;
  margin: 0 auto;
}

.contactForm textarea {
  height: 150px;
  resize: none;
}

.contactForm label {
  width: 80%;
  display: block;
  position: relative;
  margin: 10px auto;
  color: #2c3e50;
  margin-top: 40px;
}

.contactForm button {
  width: 80%;
  margin: 40px auto;
  display: block;
  position: relative;
  padding: 15px 15px;
  border-radius: 5px;
  border: 0px;
  background-color: #2c3e50;
  color: #ffffff;
  font-weight: 900;
  box-sizing: border-box;
}

.contactForm .errorName,
.contactForm .errorMessage,
.contactForm .errorPhoneNumber,
.contactForm .errorEmail {
  color: red;
  width: 80%;
  margin: 10px auto;
}

@media only screen and (min-width: 768px) {
  .contactForm input,
  .contactForm textarea {
    width: 60%;
  }
  .contactForm label {
    width: 60%;
  }
  .contactForm button {
    width: 60%;
  }
  .contactForm .errorName,
  .contactForm .errorMessage,
  .contactForm .errorPhoneNumber,
  .contactForm .errorEmail {
    width: 60%;
  }
}

.HomePage h2 {
  color: #2c3e50;
  text-align: center;
  line-height: 35px;
}

.HomePage p {
  font-size: 13px;
  line-height: 18px;
}

.HomePage h3 {
  color: #ff7043;
}
.HomePage {
  padding: 40px 40px;
  flex-grow: 1;
  display: flex;
  min-height: 70vh;
  flex-direction: column;
}

.HomePage p {
  text-align: center;
  color: #2c3e50;
}
.keyRequest {
  display: block;
}
.HomePage .key,
.HomePage .request {
  width: 90%;
  display: block;
  position: relative;
  margin: 50px auto;
  text-align: center;
  background-color: #fcfcfc;
  padding: 20px 20px;
  vertical-align: top;
  box-shadow: 2px 4px #f1f1f1;
}

.HomePage button {
  width: 75%;
  display: block;
  margin: 20px auto;
  background-color: #ff7043;
  color: #ffffff;
  font-weight: 900;
  border-radius: 5px;
  border: 0px;
  padding: 10px;
  vertical-align: bottom;
  font-size: 15px;
  text-transform: uppercase;
  box-sizing: border-box;
}

.HomePage label {
  display: inline-block;
  position: relative;
  width: 20%;
  text-align: left;
  margin: 40px 0px 10px 0px;
  color: #2c3e50;
}

.HomePage input {
  width: 55%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #d0d0d0;
}

.HomePage .small {
  font-size: 12px;
  color: #2c3e50;
}

.HomePage .small > a {
  font-size: 12px;
  color: #2c3e50;
}

@media only screen and (min-width: 768px) {
  .keyRequest {
    display: flex;
    position: relative;
    margin: 0 auto;
    width: 90%;
  }
  .HomePage .key,
  .HomePage .request {
    width: 40%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
  }
}

.dashboard {
  width: 80%;
  position: relative;
  margin: 0 auto;
  padding: 40px 40px;
  text-align: center;
  min-height: 70vh;
}

.dashboard .addItem {
  width: 250px;
  height: 220px;
  display: inline-block;
  position: relative;
  vertical-align: bottom;
  text-align: center;
  margin: 20px 15px;
  background-color: rgba(243, 166, 131, 0.3);
  padding: 20px 20px;
  border-radius: 5px;
}

.dashboard .fa-plus {
  font-size: 100px;
  color: #ffffff;
  top: 30%;
  position: absolute;
  left: 110px;
}

.dashboard .addItemText {
  color: #2c3e50;
  font-weight: 900;
  text-align: center;
}

.dashboard .centered {
  margin: 0 auto;

  display: block;
  position: relative;
}
@media only screen and (min-width: 800px) {
  .dashboard {
    text-align: left;
  }
}

.createItem {
  width: 100%;
  margin: 0 auto;
  display: block;
  position: relative;
  padding: 40px 0px;
  background-color: rgba(243, 166, 131, 0.3);
  margin-top: 80px;
}

.createItem form {
  width: 90%;
  margin: 0 auto;
}

.createItem h2,
.createItem p {
  margin: auto;
  display: block;
  position: relative;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}

.createItem p {
  margin-bottom: 80px;
}

.createItem input {
  display: block;
  position: relative;
  width: 80%;
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;

  border-radius: 5px;
  border: 1px solid #d0d0d0;
}

.createItem input[name="iconSearch"] {
  margin-bottom: 10px;
}

.createItem label {
  width: 80%;
  display: block;
  position: relative;
  margin: 10px auto;
  color: #2c3e50;
  margin-top: 40px;
}

.createItem .iconsList {
  width: 80%;
  margin: 0px auto 40px auto;
  display: block;
  position: relative;
}
.createItem .fas {
  color: #2c3e50;
  font-size: 30px;
  margin: 10px 10px 0px 0px;
}
.createItem button {
  width: 80%;
  margin: 0 auto;
  display: block;
  position: relative;
  padding: 15px 15px;
  border-radius: 5px;
  border: 0px;
  background-color: #2c3e50;
  color: #ffffff;
  font-weight: 900;
  box-sizing: border-box;
  margin-top: 40px;
}
.createItem .errorName,
.createItem .errorQuantity {
  color: red;
  width: 80%;
  margin: 10px auto;
  text-align: left;
}
.createItem .errorIcon {
  width: 100%;
  margin: 10px auto;
  text-align: left;
  color: red;
}
.createItem .selectedIcon {
  color: #ff7043;
}
@media only screen and (min-width: 768px) {
  .createItem {
    width: 50%;
  }
  .createItem input {
    width: 60%;
  }

  .createItem label {
    width: 60%;
  }

  .createItem .iconsList {
    width: 60%;
  }

  .createItem button {
    width: 60%;
  }
  .createItem form {
    width: 100%;
  }

  .createItem .errorName,
  .createItem .errorQuantity {
    width: 60%;
  }
}

.contactUs {
  width: 100%;
  margin: 0 auto;
  display: block;
  position: relative;
  padding: 20px 0px;
  background-color: rgba(243, 166, 131, 0.3);
  margin-top: 80px;
}

.contactUs h2 {
  margin: auto;
  display: block;
  position: relative;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}

@media only screen and (min-width: 768px) {
  .contactUs {
    width: 50%;
  }
}

.Error404 {
  width: 100%;

  background-size: cover;
  height: 800px;
  background-position: center center;
}

